<template>
  <div>
    <van-sticky>
    <van-nav-bar
      :title="title"
      left-arrow
      @click-left="onClickLeft"
      @click-right="onClickRight"
    >
     <van-icon name="search" slot="right" />
     </van-nav-bar>
     </van-sticky>
  </div>
</template>



<script>
//  import { Toast } from 'vant';
export default {
  props:{
    title:String
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
     
       this.$router.push({name:"search"});
     
     
    }
  }
}
</script>