<template>
<div class="itembox">
<van-pull-refresh v-model="isLoading" 
@refresh="onRefresh">
  <Item v-for="(a,i) in goods" :key="i" :good="a"> </Item>
</van-pull-refresh>    
</div>
</template>


<script>

export default {
   name: 'List2',
   props:["goods"],
    data(){
        return { 
            isLoading: false,
            }
    },

 methods: {
    onRefresh() {
      setTimeout(() => {
        this.$toast('刷新成功');
        this.isLoading = false;
      
      }, 500);
    }

},



}



</script>



<style scoped>
.itembox{
    width:100%;
    padding:0 10px;
    box-sizing: border-box;
}
</style>

