<template>
<div>
   
   <div v-if="len">
       <van-button type="default" size="large"  @click="add()">
           暂无地址，立即去新增</van-button>
  </div>

  <div v-else>
      <van-address-list
  v-model="chosenAddressId"
  :list="list"
  @add="onAdd"
  @edit="onEdit"
  @click-item="seleced"
  :addflag="addflag"
 
   >
      </van-address-list>
  </div>
  





</div>
</template>



<script>
import { Toast } from 'vant';

export default {
    data(){
        return {
      list: [],
      len:'',
      chosenAddressId:"",
      addflag:false
      
        }
    },
    created(){
       var username=window.localStorage.getItem("users");
      this.$axios.post("/vue/getAddress",{username}).then(res=>{
       // window.console.log(res)
        if(res.data.code==200){
          this.list=res.data.addressList;

          this.list=this.list.map((item,i)=>{//添加Id
            return  Object.assign(item,{id:i});
          });
          this.chosenAddressId=this.list.id;
           this.len=false;
         
        }else{
           this.len=true;
          Toast("您还没有收货地址")
        }
      })

    },
    methods:{
        onAdd(){
          this.$router.push({name:"onadd"})
        },
        add(){
        this.$router.push({name:"onadd"})
        },
        onEdit(){
          Toast("暂无后续逻辑")

        },
        seleced(){//选中id
         // alert(this.chosenAddressId)
        this.addflag=true;

        },
        //  sendMsg(){
            
        //      this.$emit('func',this.addflag)
        //  }


        

    }
    
}
</script>


